import React from 'react';

const Artist = ({artist}) =>{

    if(!artist) return null;
    const {images,name,followers,genres} = artist;

    return(
        <div>
            <h3>{name}</h3>
            <p>{followers.total} followers</p>
            <p>{genres.join(',')}</p>
            <img 
                src={images[0] && images[0].url}
                style={{
                    width:"200px",
                    height:"200px",
                    borderRadius:100,
                    objectFit:'cover'
                }}
            
            />

        </div>
    )

}

export default Artist; 